<template>
    <nav class="navbar navbar-expand-lg bg-body-tertiary" v-if="navshou">
      <div class="container-fluid">
        <router-link class="navbar-brand" to="/">代码展示</router-link>
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav">
            <li class="nav-item">
              <routerLink :class="other" li to="/">其他代码</routerLink>
            </li>
            <li>
              <routerLink :class="lisan" li to="/lisan">离散上机</routerLink>
            </li>
            <li>
              <routerLink :class="shumo" li to="/shumo">数模</routerLink>
            </li>
          </ul>
        </div>
      </div>
    </nav>
</template>

<script setup>
// import { RouterLink } from 'vue-router';
// import { computed } from 'vue';
import { useRoute } from 'vue-router';
import {  ref, watch } from 'vue';
const route = useRoute()
const userData = ref()
let act = "nav-link active"
let unact = "nav-link"
let lisan = ref("nav-link")
let other = ref("nav-link")
let shumo = ref("nav-link")
let navshou = ref(true);
watch(
  () => route.path,
   (newId) => {
    userData.value = newId
    console.log(userData.value)
     if (userData.value === '/') {
       other.value = act;
       lisan.value = unact;
       shumo.value = unact
     } else if (userData.value === '/lisan') {
       lisan.value = act;
       other.value = unact;
       shumo.value = unact;
     } else if (userData.value === '/shumo') {
       lisan.value = unact;
       other.value = unact;
       shumo.value = act;
     }
     if (userData.value === '/lisan' || userData.value === '/' || userData.value === '/shumo') {
       console.log(1);
       navshou.value = true;
     } else {
       console.log(2);
       navshou.value = false;
     }
  }
)

</script>

